// 瀑布流组件
import kWaterfallFlow from '@/components/k-ui/k-waterfall-flow/k-waterfall-flow.vue';
// 筛选组件
import slFilter from '@/components/other/sl-filter/sl-filter.vue';
// 回调顶部
import backTop from '@/components/other/back-top/back-top.vue';
export default {
	components: {
		kWaterfallFlow,
		slFilter,
		backTop
	},
	data() {
		return {
			currentPage: 1,
			//回到顶部
			backTop: {
				src: 'http://www.iamk.top/static/back-top/top.png',
				scrollTop: 0
			},
			// 瀑布流数据
			users: [],
			loading: true,
			restart: false,
			// 筛选组件
			themeColor: '#008de1',
			filterResult: '',
			menuList: [
				// {
				// 	'title': '技能',
				// 	'detailTitle': '请选择技能类型（可多选）',
				// 	'isMutiple': true,
				// 	'key': 'tag',
				// 	'detailList': [{
				// 			'title': '不限',
				// 			'value': ''
				// 		},
				// 	]
				// },
				{
					'title': '学院',
					'key': 'academy',
					'isMutiple': true,
					'detailTitle': '请选择学院（可多选）',
					'detailList': [{
							'title': '不限',
							'value': ''
						}
					]
				},
				{
					'title': '年级',
					'key': 'grade',
					'isMutiple': true,
					'detailTitle': '请选择小伙伴年级（可多选）',
					'detailList': [{
							'title': '不限',
							'value': ''
						}
					]
				},
				{
					'title': '排序',
					'key': 'sort',
					'isSort': true,
					'detailList': [{
							'title': '默认',
							'value': ''
						},
						{
							'title': '访问量',
							'value': 1
						},
						{
							'title': '点赞量',
							'value': 2
						}
					]
				}
			]
		}
	},
	onPageScroll(e) {
		this.backTop.scrollTop = e.scrollTop;
	},
	onShow() {
		this.loadData()
	},
	onLoad() {
		this.loadMenuList()
	},
	onReachBottom() {
		if (this.users.length < this.currentPage * this.$common.pageSize) return;
		this.currentPage++;
		this.loading = true;
		this.restart = false
		this.loadData()
	},
	methods: {
		goList(value) {
			uni.navigateTo({
				url: '../list/list?type=' + value.type + '&id=' + value.id
			})
		},
		clickItem() {
			this.$api.msg(`分类详情`);
		},
		// 用户详情
		choose(item) {
			// item 返回选中 JSON 对象
			console.log(item)
			uni.navigateTo({
				url: '/pages/person/person-detail?id=' + item.id
			})
		},
		// 筛选结果
		result(val) {
			this.filterResult = JSON.parse(JSON.stringify(val))
			console.log(this.filterResult)
			this.loading = true
			this.currentPage = 1
			this.users = []
			this.restart = true
			this.loadData()
		},
		async loadData() {
			if (this.users.length < this.currentPage * this.$common.pageSize) {
				let res = await this.$api.request({
					url: '/user/users?currentPage=' + this.currentPage,
					method: 'POST',
					data: {
						key: '',
						tag: JSON.stringify(this.filterResult.tag) || '',
						academy: JSON.stringify(this.filterResult.academy) || '',
						sort: this.filterResult.sort || '',
						grade: JSON.stringify(this.filterResult.grade) || '',
						pageSize: this.$common.pageSize
					}
				})
				console.log(res)
				setTimeout(() => {
					this.users = [...this.users, ...res.data]
					// console.log("this.users.length: " + this.users.length)
					setTimeout(() => {
						this.loading = false;
						// this.restart = false
					}, 120);
				}, 1000)
			}
		},
		loadMenuList(){
			// 学院
			this.getAcademies()
			// 年级
			const date = new Date();
			const year = date.getFullYear()
			let i=0;
			for(;i<=5;i=i+1){
				const grade = {
					'title': String(year-i),
					'value': String(year-i)
				}
				this.menuList[1].detailList.push(grade)
			}
		},
		async getAcademies() {
			const res = await this.$api.request({
				url: '/academy',
				method: 'GET',
			})
			const _this = this
			res.data.forEach(function(r){
				const a = {
					'title': r.name,
					'value': r.id
				}
				_this.menuList[0].detailList.push(a)
			})
		},
	}
}
